import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import 'hammerjs';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatToolbarModule, MatMenuModule, MatCardModule, MatButtonModule, MatInputModule, MatProgressSpinnerModule } from '@angular/material';

import { AppComponent } from './app.component';
import { YoutubeApiService } from './youtube-api.service';
import { HeaderComponent } from './header/header.component';
import { ContentComponent } from './content/content.component';
import { FooterComponent } from './footer/footer.component';
import { CardComponent } from './card/card.component';
  
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    ContentComponent,
    FooterComponent,
    CardComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MatToolbarModule, MatMenuModule, MatCardModule, MatButtonModule, MatInputModule,
    MatProgressSpinnerModule
  ],
  exports: [MatButtonModule, MatToolbarModule],
  providers: [YoutubeApiService],
  bootstrap: [AppComponent]
})
export class AppModule { }



