import { Component, OnInit, Input, OnChanges, SimpleChanges, SimpleChange } from '@angular/core';
import { MatCardModule } from '@angular/material';
import { YoutubeApiService } from './../youtube-api.service';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.css']
})
export class CardComponent implements OnChanges{

  infoApi: string;
  imagesUrls: Array<any>;
  resultsPerPage: any;

  @Input() textSearch: string; // To receive message from the parent component (app)

  constructor(private yt: YoutubeApiService) {}

  fetchVideos(){
    this.yt.searchVideos(this.textSearch)
    .subscribe((infoApi: string) => {  

      this.infoApi = infoApi;

      this.imagesUrls = this.infoApi['items'];
    })
  }

  ngOnChanges(changes: SimpleChanges) {
      const textSearch: SimpleChange = changes.textSearch;

      this.fetchVideos();
  }
}



